import React, { useEffect, useState, useReducer } from "react";
import RTChart from "react-rt-chart";
import { CCol, CRow } from "@coreui/react";
import BlocData from "../bigPicture/BlocData";
import store from "../../store/store";
import { useSelector } from "react-redux";

const ApexChart = () => {
  const p_load = useSelector((state) => {
    console.log(state);
    return state.p_load;
  });
  const p_solar_panel = useSelector((state) => state.p_solar_panel);
  const battery_SoC = useSelector((state) => state.battery_SoC);
  const p_mppt = useSelector((state) => state.p_mppt);
  const p_battery = useSelector((state) => state.p_battery);
  const p_inv = useSelector((state) => state.p_inv);
  const p_charger = useSelector((state) => state.p_charger);
  var SocBattery;
  var puissance = 0;
  SocBattery = battery_SoC;
  switch (window.location.href.split("/#/")[1]) {
    case "PanneauSolaire" || "data":
      puissance = p_solar_panel;
    case "batterie":
      puissance = p_battery;
    case "onduleur":
      puissance = p_inv;
    case "mppt":
      puissance = p_mppt;
    case "chargeur":
      puissance = p_charger;
    case "sortie1":
      puissance = p_load;
    default:
  }
  var data = {
    date: new Date(),
    puissance: puissance !== 0 ? puissance : 0,
    SocBattery: SocBattery * 100,
    Puissance_sauv: 0.6,
    Argent: 5,
  };
  var flow = { duration: 500 };

  return (
    <>
      <h1 style={{ padding: "30px 10px 10px 10px", textAlign: "center", color: "red" }}>En Construction</h1>
      <h1 class="sub-title_donnes">Données temps réel</h1>
      <CRow style={{ padding: "20px 30px" }}>
        <CCol>
          <BlocData label="Puissance" unit="W" data={data.puissance} />
        </CCol>
      </CRow>
      <h1 class="sub-title_donnes">Historique de données</h1>
      <RTChart fields={["puissance"]} data={data} flow={flow} style={{ height: "350px" }} maxValues={10} />
    </>
  );
};
export default ApexChart;
